@use "../../scss/mixin";
@use "../../scss/variables";

.blurryLoading__container {
    position: relative;
    width: 100vw;
    height: 100vh;
    @include mixin.FlexCenter;
}

.bg {
    background: url('https://images.unsplash.com/photo-1576161787924-01bb08dad4a4?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2104&q=80') no-repeat center center/cover;
    @include mixin.sameParentWH;
    z-index: -1;
    filter: blur(0px);
    position: absolute;
    top: 0;
}

.loading-text {
    font-size: 60px;
    color: variables.$color-light;
}